
import { fetchData } from "@/services/api";
import { AxiosError } from "axios";
import { defineComponent } from "vue";

export default defineComponent({
    name: "ItemView",
    components: {},
    data() {
        return {
            item: (): (unknown & { url: string }) | null => null,
            error: "",
            checkArray: [
                "films",
                "species",
                "starships",
                "vehicles",
                "characters",
                "planets",
                "people",
                "homeworld",
                "pilots",
                "residents",
                "additional",
            ],
            notShowingArray: ["created", "edited", "url", "opening_crawl"],
        };
    },

    methods: {
        async getItem() {
            try {
                const res = await fetchData(`${this.itemClass}/${this.id}`);
                this.item = res;
            } catch (error) {
                if ((error as AxiosError).response?.status === 404) {
                    this.error = "Page was not founded";
                } else {
                    this.error = (error as AxiosError).message;
                }
                console.log(error);
            }
        },
    },

    created() {
        this.getItem();
    },

    computed: {
        id() {
            return this.$route.params.id;
        },
        routeName() {
            return this.$route.name;
        },
        itemClass() {
            return this.$route.name?.toString().split("-")[0];
        },
    },

    watch: {
        item() {
            return;
        },
    },
});
